import { render , html, ViewTemplate , ViewContext } from '@lithium-framework/core';
import '@lithium-framework/router-element'


let template: ViewTemplate<any> = html`${( context:ViewContext )=>{


  return html`<div class='cours'>
        <nav class='header-menu'>
            <a href='compte'>Compte</a>
            <a href='/' id='logout'>Déconnexion</a>
        </nav>
        <div class='content'>
            <nav class='horizontal-menu'>
                <a href='cours'>S'inscrire aux cours</a>
                <a href='informations'>Informations supplémentaires</a>
            </nav>
            <div class='content-classes'>
                <h1>Inscriptions aux cours</h1>
            </div>
        </div>
    </div>`;

}}`

document.addEventListener('DOMContentLoaded', () => {

    const messageContainer = document.createElement('div');
    const content = document.querySelectorAll('.content-classes')[0];

    fetch('http://localhost:3000/cours/', {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    })
    .then(response => {
        if (!response.ok) {
            throw new Error('Erreur serveur.');
        }
        return response.json();
    })
    .then(data => {
        if (data.length > 0) {
            for(let item of data){
                console.log(item.date_cours)
                const raw = document.createElement('div');
                raw.innerHTML = `${item.date_cours} - ${item.heure_debut} - ${item.heure_fin} - ${item.type_cours}`;
                content?.appendChild(raw);
            }
        } else {
            messageContainer.textContent = 'Utilisateur non trouvé ou mot de passe incorrect.';
        }
        console.log('Réponse du serveur:', data);
    })
    .catch(error => {
        messageContainer.innerHTML = "Vous n'êtes pas encore inscris, veuillez vous <a href='inscriptions'><strong>inscrire</string></a>";
        console.error('Erreur lors de la requête fetch:', error);
    });
    
    const logoutLink = document.getElementById('logout');

    if (logoutLink) {
        logoutLink.addEventListener('click', (e) => {
            
            e.preventDefault();

            localStorage.clear();
            sessionStorage.clear();

            window.location.href = '/';
        });
    }
});




render(template);